import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    scrollBehavior () {
        return { x: 0, y: 0 }
    },
    routes: [

        {
    // =============================================================================
    // MAIN LAYOUT ROUTES
    // =============================================================================
            path: '',
            component: () => import('./layouts/main/Main.vue'),
            children: [
        // =============================================================================
        // Theme Routes
        // =============================================================================
              {
                path: '/',
                name: 'home',
                component: () => import('./views/Home.vue')
              },
              {
                path: '/shipments',
                name: 'shipments',
                component: () => import('./views/Shipments.vue')
              },
              {
                path: '/shipments/details',
                name: 'details',
                component: () => import('./views/Details.vue'),
                meta: {
                    breadcrumb: [
                      { title: 'Home', url: '/' },
                      { title: 'Shipments', url: '/shipments'},
                      { title: 'Shipment Details', active: true },
                    ],
                    pageTitle: 'Shipment Details'
                }
              },
              {
                path: '/drivers',
                name: 'drivers',
                component: () => import('./views/Drivers.vue')
              },
              {
                path: '/vehicles',
                name: 'vehicles',
                component: () => import('./views/Vehicles.vue')
              },
              {
                path: '/bids',
                name: 'bids',
                component: () => import('./views/Bids.vue')
              },
              {
                path: '/dispatchers',
                name: 'dispatchers',
                component: () => import('./views/Dispatchers.vue')
              },
              {
                path: '/dispatchers/view',
                name: 'userview',
                component: () => import('./views/UserView.vue'),
                meta: {
                  breadcrumb: [
                    { title: 'Home', url: '/' },
                    { title: 'Dispatchers', url: '/dispatchers'},
                    { title: 'Dispatcher Details', active: true },
                  ],
                  pageTitle: 'Dispatcher Details'
                }
              },
              {
                path: '/dispatchers/edit',
                name: 'useredit',
                component: () => import('./views/UserEdit.vue'),
                meta: {
                  breadcrumb: [
                    { title: 'Home', url: '/' },
                    { title: 'Dispatchers', url: '/dispatchers'},
                    { title: 'Dispatchers Details', url: '/dispatchers/view'},
                    { title: 'Dispatcher Settings', active: true },
                  ],
                  pageTitle: 'Dispatcher Settings'
                }
              },
              {
                path: '/owners',
                name: 'owners',
                component: () => import('./views/Owners.vue')
              }
            ],
        },
    // =============================================================================
    // FULL PAGE LAYOUTS
    // =============================================================================
        {
            path: '',
            component: () => import('@/layouts/full-page/FullPage.vue'),
            children: [
        // =============================================================================
        // PAGES
        // =============================================================================
              {
                path: '/login',
                name: 'page-login',
                component: () => import('@/views/pages/Login.vue')
              },
              {
                path: '/pages/error-404',
                name: 'page-error-404',
                component: () => import('@/views/pages/Error404.vue')
              },
            ]
        },
        // Redirect to 404 page, if no match found
        {
            path: '*',
            redirect: '/pages/error-404'
        }
    ],
})

router.afterEach(() => {
  // Remove initial loading
  const appLoading = document.getElementById('loading-bg')
    if (appLoading) {
        appLoading.style.display = "none";
    }
})

export default router
